import React from 'react';
import '../css/Modal.css';

const Modal = (props) => {
    // , ,     
    const { open, close } = props;

    return (
        //   openModal  .
        <div className={open ? 'openModal modal' : 'modal'}>
            {open ? (
                <section>
                    <header>
                          
                        <button className="close" onClick={close}>
                            &times;
                        </button>
                    </header>
                    {props.children}
                </section>
            ) : null}
        </div>
    );
};

export default Modal;