import React, { useState } from 'react'; //Hooks  useState
import Modal from './Modal';
import SpaceModal from './SpaceModal';

function InputText() {
    //
    const style = {
        width: "300px",
        padding: '10px',
        margin: 'auto',
        /*border: '1px solid black'*/
    };

    const styleStart = {
        color:"white",
        borderColor: "black",
        borderWidth: 2,
        borderRadius: 100,
        borderStyle: 'solid',
        textDecoration: 'none',
        backgroundColor: "black",
        marginLeft: "10px"
    };

    //useState() -    
    const [catId, setCatId] = useState(''); //  
    const [catImg, setCatImg] = useState(''); //  

    //state   - input   
    const onTextChange = (e) => {
        //e :  
        setCatId(e.target.value); //   value 
    };

    const [modalOpen, setModalOpen] = useState(false);
    const [spaceModalOpen, setSpaceModalOpen] = useState(false);

    const openModal = () => {

        console.log({catId});

        if({catId}.catId === '' || {catId}.catId === ' '){
            setSpaceModalOpen(true);
        }else {
            let index = parseInt(document.getElementsByClassName('slick-current')[0].getAttribute("data-index"));
            setCatImg(index + 1);

            setModalOpen(true);
        }

    };
    const closeModal = () => {
        setModalOpen(false);
    };

    const closeSpaceModal = () => {
        setSpaceModalOpen(false);
    };

    const onKeyPress = (e) => {
        if(e.key === 'Enter') {
            openModal();
        }
    };

    return (
        <div style={ style }>
              : <input type="text" onKeyPress={onKeyPress} onChange={onTextChange} value={catId}/>
            <button style={ styleStart } onClick={openModal}>
                
            </button>
            <Modal open={modalOpen} close={closeModal} catId={ catId } catImg={ catImg }>
                <main> { catId } </main> ?
            </Modal>
            <SpaceModal open={spaceModalOpen} close={closeSpaceModal}>
                <main> !    ! </main>
            </SpaceModal>
        </div>
    );
}

export default InputText;