import React, { useState } from "react";
import { useForm } from 'react-hook-form';
import { Navigate } from 'react-router-dom';
import axios from 'axios';

function LoginPage() {
    const { register, handleSubmit, formState: { errors } } = useForm();
    const [showPopup, setShowPopup] = useState(false);

    const formStyle = {
        textAlign: "center",
        marginTop: "15%",
        border: "solid 1px",
        width: "300px",
        marginLeft: "43%"
    };

    const btnStyle = {
        color:"white",
        borderColor: "black",
        borderWidth: 2,
        borderRadius: 100,
        borderStyle: 'solid',
        textDecoration: 'none',
        backgroundColor: "black",
    };

    const onSubmit = (data) => {
        axios
            .post('/login', data)
            .then((response) => {

                console.log(response);

                if (response.data === '') {
                    alert("id  pw ");
                    window.location.replace("/login");
                }else {
                    alert(" ");
                    localStorage.setItem("memberId", response.data.memberId);
                    localStorage.setItem("memberName", response.data.memberName);
                    //   
                    setShowPopup(true);
                }
            })
            .catch((error) => {
                console.log(error);
            });

    };

    const handleExistingCat = () => {
        //      /contents  
        window.location.href = "/contents";
    };

    const handleNewCat = () => {
        //      /choice  
        window.location.href = "/choice";
    };

    return (
        <form style={formStyle} onSubmit={handleSubmit(onSubmit)}>
            <h1> </h1>
            <div>
                <input type="text" placeholder="" {...register("memberId", { required: true })} />
                {errors.memberId && <span> .</span>}
            </div>
            <div>
                <input type="password" placeholder="" {...register("memberPw", { required: true })} />
                {errors.memberPw && <span> .</span>}
            </div>
            <button style={btnStyle} type="submit"></button>

            {showPopup && (
                <div>
                    <p>{window.localStorage.getItem("memberName")}  .</p>
                    <button style={btnStyle} onClick={handleExistingCat}>  </button>
                    <button style={btnStyle} onClick={handleNewCat}>  </button>
                </div>
            )}
        </form>
    );
}

export default LoginPage;