import React from 'react';
import '../css/Modal.css';

const DeleteModal = (props) => {
    // , ,     
    const { open, close } = props;

    const footerStyle = {
        textAlign: 'center'
    };

    return (
        //   openModal  .
        <div className={open ? 'openModal modal' : 'modal'}>
            {open ? (
                <section>
                    <header>
                        <button className="close" onClick={close}>
                            &times;
                        </button>
                    </header>
                    <main>
                        {props.date}    ?
                    </main>
                    {props.children}
                </section>
            ) : null}
        </div>
    );
};

export default DeleteModal;