import React, { useState, useEffect } from 'react';
import Calendar from 'react-calendar';
import '../css/Records.css'
import { motion } from "framer-motion";
import moment from "moment";
import CalModal from './CalModal';
import DeleteModal from './DeleteModal';
import MiniModal from './MiniModal';
import ViewModal from './ViewModal';
import {useLocation, useNavigate} from "react-router-dom";
import axios from "axios";


export default function Records() {

    const calStyle = {
        width: '100vw',
        height: '100vh',
    };

    const mainCalStyle = {
        marginTop: '20%',
        '@media (minWidth: 800px)': {
            width: '50%',
            marginLeft: '25%',
        }
    };

    const titleStyle = {
        textAlign: 'center',
        fontSize: 'xxx-large'
    };

    const [catList, setCatList] = useState([]);
    const [viewList, setViewList] = useState([]);

    useEffect(() => {
        const localStorageArray = JSON.parse(localStorage.getItem("catList")) || [];
        setCatList(localStorageArray);
    }, []);

    const [selectedCat, setSelectedCat] = useState('');

    function handleChange(event) {
        setSelectedCat(event.target.value);
    }

    const [value, onChange] = useState(new Date());
    const [value2, onChange2] = useState(new Date());
    const [modalOpen, setModalOpen] = useState(false);
    const [modalViewOpen, setModalViewOpen] = useState(false);
    const [modalDeleteOpen, setModalDeleteOpen] = useState(false);
    const [miniModalOpen, setMiniModalOpen] = useState(false);
    const [inputs, setInputs] = useState({
        purpose: '',
        records:'',
    });

    const [marks, setMarks] = useState([]);

    const {purpose, records} = inputs;

    const onChangeInputText = (e) => {
        const {name, value} = e.target;
        const nextInputs = {
            ...inputs,
            [name]: value, //e.target name value.
        };

        setInputs(nextInputs)
    };

    const openModal = () => {
        setModalOpen(true);
    };
    const closeModal = () => {
        setModalOpen(false);
    };
    const openViewModal = () => {
        setModalViewOpen(true);
    };
    const closeViewModal = () => {
        setModalViewOpen(false);
    };
    const openModalDelete = () => {
        setModalDeleteOpen(true);
    };
    const closeModalDelete = () => {
        setModalDeleteOpen(false);
    };
    const openMiniModal = () => {
        setMiniModalOpen(true);
    };
    const closeMiniModal = () => {
        setMiniModalOpen(false);
    };

    const recordsData = {
        memberId:localStorage.getItem('memberId'),
        catName:selectedCat,
        purpose:purpose,
        records:records,
        choiceDate:moment(value).format("DD-MM-YYYY")/*,
        visitDate:moment(value2).format("DD-MM-YYYY")*/
    };

    const viewRecords = () => {
        for(let i=0; i < marks.length; i++) {
            if ((moment(value).format("DD-MM-YYYY") === marks[i])){
                setModalViewOpen(true);
            }
        }

        axios.get("/records",
            {params : recordsData})
            .then( function(response) {
                setViewList(response.data);

                if(response.data === "") {
                    alert("  .");
                }
            })
            .catch(function(error){
                console.log(error);
            });


    };

    const createRecords = () => {
        let dupCheck;

        for(let i=0; i < marks.length; i++) {
            if ((moment(value).format("DD-MM-YYYY") === marks[i])){
                dupCheck = true;
            }
        }

        if (dupCheck === true) {
            alert("   .");
        }else {
            axios.post("/records",recordsData)
                .then( function(response) {
                    console.log(response);
                    alert("  .");
                    window.location.reload();
                })
                .catch(function(error){
                    console.log(error);
                    alert("  .");
                });

            setModalOpen(false);
        }

    };

    /*const updateRecords = () => {

        axios.put("/records",recordsData)
            .then( function(response) {
                console.log(response);
                alert("  .");
            })
            .catch(function(error){
                console.log(error);
                alert("  .");
            });

        setModalOpen(false);
    };*/

    const deleteRecords = () => {

        axios.delete("/records",
            {params : recordsData})
            .then( function(response) {
                alert("  .");
                window.location.reload();
            })
            .catch(function(error){
                console.log(error);
                alert("  .");
            });

        setModalDeleteOpen(false);
    };

    const footerStyle = {
        textAlign: 'center'
    };

    const textStyle = {
        width: "100%",
        height: "250px",
        resize: "none"
    };

    const styleStart = {
        color:"white",
        borderColor: "black",
        borderWidth: 2,
        borderRadius: 100,
        borderStyle: 'solid',
        textDecoration: 'none',
        backgroundColor: "black",
    };



    useEffect(() => {
        axios.post("/recordsList", {memberId : localStorage.getItem('memberId')})
            .then(function(response) {
                setMarks(response.data);
        })
        .catch(function(error){
            console.log(error);
            alert("   ");
            localStorage.clear();
            navigate("/");
        });
    }, []);

    const navigate = useNavigate();

    const goToPrev = () => {
        navigate("/contents");
    };

    return (
        <motion.div initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}} transition={{ duration: 1 }}>
            <h1 style={titleStyle}> </h1>
            <Calendar style={mainCalStyle} onChange={onChange} value={value}
              tileClassName={({ date, view }) => {
                if (marks.find((x) => x === moment(date).format("DD-MM-YYYY"))) {
                    return "highlight";
                }
              }}
            />
            <button style={styleStart} onClick={viewRecords}>
                 
            </button>
            <button style={styleStart} onClick={openModal}>
                 
            </button>
            <button style={styleStart} onClick={openModalDelete}>
                 
            </button>
            <button style={styleStart} onClick={goToPrev}>
                 
            </button>

            {/*  */}
            <ViewModal open={modalViewOpen} close={closeViewModal}>
                <main>
                      : <input value={viewList.memberId} readOnly={true}/>
                    <br/>
                      : <input value={viewList.catName} readOnly={true}/>
                    <br/>
                      : <input value={moment(value).format("YYYY MM DD")} readOnly={true}/>
                    <br/>
                      : <input value={viewList.purpose} readOnly={true}/>
                    <br/>
                     
                    <textarea style={textStyle} value={viewList.records} readOnly={true}/>
                    {/*  : <input/>
                    <br/>
                      : <input onClick={openMiniModal} readOnly value={moment(value2).format("YYYY MM DD")}/>*/}
                </main>
                <footer style={footerStyle}>
                    {/*<button className="submit" onClick={updateRecords}>
                         
                    </button>*/}
                    <button className="close" onClick={closeViewModal}>
                        
                    </button>
                </footer>
            </ViewModal>
            {/*  */}
            <CalModal open={modalOpen} close={closeModal}>
                <main>
                      : <input value={window.localStorage.getItem('memberId')} readOnly={true}/>
                    <br/>
                      :
                    <select onChange={handleChange}>
                        {catList.map(cat => (
                            <option key={cat.catSeq} value={cat.catName}>{cat.catName}</option>
                        ))}
                    </select>
                    <br/>
                      : <input readOnly value={moment(value).format("YYYY MM DD")}/>
                    <br/>
                      : <input name="purpose" onChange={onChangeInputText}/>
                    <br/>
                     
                    <textarea style={textStyle} name="records" onChange={onChangeInputText}/>
                    {/*  : <input/>
                    <br/>
                      : <input onClick={openMiniModal} readOnly value={moment(value2).format("YYYY MM DD")}/>*/}
                </main>
                <footer style={footerStyle}>
                    <button className="submit" onClick={createRecords}>
                        
                    </button>
                    <button className="close" onClick={closeModal}>
                        
                    </button>
                </footer>
            </CalModal>
            {/*  >  */}
            <MiniModal open={miniModalOpen} close={closeMiniModal}>
                <Calendar style={calStyle} onChange={onChange2} value={value2}/>
            </MiniModal>
            {/*  */}
            <DeleteModal open={modalDeleteOpen} close={closeModalDelete} date={moment(value).format("YYYY MM DD")}>
                <footer style={footerStyle}>
                    <button className="submit" onClick={deleteRecords}>
                        
                    </button>
                    <button className="close" onClick={closeModalDelete}>
                        
                    </button>
                </footer>
            </DeleteModal>

        </motion.div>
    );
}
